import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router";
import "./Detail.scss";

function Detail(props) {
  let [alert, alert변경] = useState(true);
  useEffect(() => {
    let 타이머 = setTimeout(() => {
      alert변경(false);
    }, 2000);
    return () => {
      clearTimeout(타이머); //2초 전에 나갔을 때 버그 방지용
    };
  }, []);
  // useEffect 안 콜백함수 안에는 컴포넌트가 첫 등장, 업데이트 시 실행할 것
  // return 안 함수는 컴포넌트가 사라질 때 실행할 것
  //업데이트 되어도 실행 안되게 하는 법 끝에 [] 붙이기

  let history = useHistory();
  let { id } = useParams(); // {id}는 :id 자리에 있던 숫자
  let 찾은상품 = props.shoes.find((x) => x.id == id); // 콜백함수에 function(상품) {return 상품.id == id} 로 써도 됨
  // find() 는 array 뒤에 위치, 안에 콜백 함수 필요
  // shoes 안의 id라는 영구번호 = url의 /:id 자리 숫자인지 찾아라!

  return (
    <div className="container">
      {alert === true ? (
        <div className="my-alert">
          <p>재고가 얼마 안 남았어요!</p>
        </div>
      ) : null}

      <div className="row">
        <div className="col-md-6">
          <img
            src={
              "https://codingapple1.github.io/shop/shoes" +
              (찾은상품.id + 1) +
              ".jpg"
            }
            width="100%"
          />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{찾은상품.title}</h4>
          <p>{찾은상품.content}</p>
          <p>{찾은상품.price}원</p>

          <Info 재고={props.재고}></Info>

          <button
            className="btn btn-danger"
            onClick={() => {
              props.재고변경([9, 10, 11]);
            }}
          >
            주문하기
          </button>
          <button
            className="btn btn-danger"
            onClick={() => {
              history.goBack(); //history.push('/') 링크로 이동
            }}
          >
            뒤로가기
          </button>
        </div>
      </div>
    </div>
  );
}

function Info(props) {
  return <p>재고: {props.재고[0]} </p>;
}
export default Detail;
